*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    font-family: 'poppins';
    /* background-color: blue; */
}
.inputBox{
    position: relative;
    width: 100%;
}
.inputBox input{
    padding: 9px;
    border: 2px solid rgb(238, 234, 234);
    border-radius: 9px;
    width: 100%;
    outline: none;
    font-size: 1.4em;
    background-color: white;
    /* color: aqua; */
    /* pointer-events: none; */
}
.inputBox input:focus{
    border: 2px solid blue;
}
.inputBox2 input:focus{
    border: 2px solid blue;
}
.inputBox span{
    position: absolute;
    width: 126px;
    left: 7%;
    padding: 12px ;
    /* text-align: center; */
    /* color: aqua; */
    pointer-events: none;
    transition: 0.5s;
}
.inputBox input:valid ~ span,
.inputBox input:focus ~ span
{
    color: rgb(43, 7, 248);
    -webkit-transform: translateX(9px) translateY(-19px);
            transform: translateX(9px) translateY(-19px);
    padding: 0px 0px 0px 14px;
    left: 2%;
    background-color: white;
    font-size: 0.79em;
    /* border-left: 2px solid gold;
    border-right: 2px solid gold; */
    /* text-align:center; */
    /* background-color: orange; */
    /* border-left: 2px solid blue; */
    /* border-right: 2px solid blue; */

}

.inputBox2 {
    position: relative;
    width: 100%;
}
.inputBox2 input{
    padding: 9px;
    border: 2px solid rgb(238, 234, 234);
    border-radius: 9px;
    width: 99%;
    outline: none;
    font-size: 1.4em;
    background-color: white;
    /* color: aqua; */
    /* pointer-events: none; */
}
.inputBox2 span{
    position: absolute;
    width: 200px;
    left: 7%;
    padding: 12px ;
    /* left: 4%;
    padding: 9px; */
    /* text-align: center; */
    /* color: aqua; */
    pointer-events: none;
    transition: 0.5s;
}
.inputBox2 input:valid ~ span,
.inputBox2 input:focus ~ span
{
    left: 2%;
    color: rgb(25, 0, 255);
    -webkit-transform: translateX(9px) translateY(-19px);
            transform: translateX(9px) translateY(-19px);

    padding: 0 5px;
    background-color: white;
    font-size: 0.79em;
    /* border-left: 2px solid blue; */
    /* border-right: 2px solid blue; */
}

#index{
    position: relative;
    width: 100%;
    height: auto;
    background-color: rgb(250, 246, 246);
}
.index2{
    position: absolute;
    /* width: 50%; */
}
.userInput{
    width: 85%;
    margin: 5px 5px;
    padding: 6px;
    border-style: double;
    border-color: beige;
    border-width: unset;
